/* Собственно сам слайдер */
.slider {
  max-width: 100%;
  position: relative;
  height: 300px;
  margin-bottom: 15px;
  box-shadow: 0px 0px 15px 0px #ebb251; }

/* Картинка мастабируется по отношению к родительскому элементу */
.slider .item img {
  object-fit: cover;
  width: 100%;
  height: 300px;
  border: none !important;
  box-shadow: none !important; }

/* Кнопки вперед и назад */
.prev {
  position: relative; }

.prev::before {
  position: absolute;
  content: "";
  background-image: url(../img/slide-left.svg);
  width: 9px;
  height: 12px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  top: 1px;
  left: -25px;
  cursor: pointer; }

.next {
  position: relative; }

.next::before {
  position: absolute;
  content: "";
  background-image: url(../img/slide-right.svg);
  width: 9px;
  height: 12px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  top: 1px;
  left: 15px;
  cursor: pointer; }

.slider .prev,
.slider .next {
  cursor: pointer;
  position: absolute;
  top: 0;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0; }

.slider .next {
  right: 0;
  border-radius: 3px 0 0 3px; }

/* При наведении на кнопки добавляем фон кнопок */
.slider .prev:hover,
.slider .next:hover {
  background-color: rgba(0, 0, 0, 0.8); }

/* Заголовок слайда */
.slide-title {
  position: absolute;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 18px;
  text-transform: uppercase;
  color: #ffb548;
  top: 200px;
  left: 10px; }

.slide-text {
  position: absolute;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 14px;
  color: #b7b7b7;
  top: 230px;
  left: 10px; }

.slide-date {
  position: absolute;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 15px;
  color: #999999;
  top: 7px;
  left: 405px; }

/* Кружочки */
.slider-dots {
  width: 90px;
  margin: 0 auto; }

.slider-dots_item {
  cursor: pointer;
  height: 14px;
  width: 14px;
  margin: 0 2px;
  border: 1px solid #8b6f47;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease; }

.active,
.slider-dots_item:hover {
  background-image: url(../img/dot-active.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 14px;
  height: 14px; }

/* Анимация слайдов */
.slider .item {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s; }

@-webkit-keyframes fade {
  from {
    opacity: 0.4; }
  to {
    opacity: 1; } }

@keyframes fade {
  from {
    opacity: 0.4; }
  to {
    opacity: 1; } }

